@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.container {
  overflow: auto;
  max-height: 80%;
  justify-content: left;
}

/*   header  */
.header {
  margin-top: 0px;
  height: 150px;
  background-color: #ebedf0;
  opacity: 1;
  display: grid;
  grid-template-columns: auto auto auto auto auto auto;
  grid-gap: 10px;
  position: sticky;

  position: fixed;
  left: 0px;
  top: 0px px;


  overflow: visible;
  width: 100%;

}

.img {
  margin-left: 200px;
  width: 150px;
  height: 150px;
  grid-row: 1 / span 2;
}

.button {
  display: flexbox;
  color: black;
  font-size: 18px;
  border: none;
  text-transform: uppercase;
  margin: 45px;
  width: 100px;
  height: 100px;
  cursor: pointer;
  background: none;
}

.button:hover {
  color: white;
  background: #2d2928;
  ;
  text-transform: uppercase;
  cursor: pointer;
  font-size: 16px;
}

/* body */

.bgImg {
  height: 1000px;
  background-repeat: no-repeat;
}

.intro {
  margin: auto;
  margin-left: 40px;
  border-radius: 5px;
  opacity: 1;
  text-align: center;
}

.intro p {
  color: #2d2928;
  padding: 20px;
  font-size: 30px;
}

/* about detail */

.about {
  width: 100%;
  padding-left: 50px;
  clear: both;
  background-color: #ebedf0;
  border-bottom: solid #d7d9dc 1px;
  overflow: hidden;
  background-size: cover;
}

.about h1 {
  width: 50%;
  z-index: 0;
  margin: 3% auto;
  color: #555;
  z-index: 0;
}

.about h2 {
  width: 50%;
  color: #555;
  margin: 2% auto;
  font-size: 20px;
}

/*location detail */
.locationDetail {
  width: 100%;
    padding-left: 50px;
    clear: both;
    background-color: #ebedf0;
    border-bottom: solid #d7d9dc 1px;
    overflow: hidden;
    background-size: cover;
}

.locationDetail h2 {
  margin-top: 20px;
  text-align: center;
}

.locationDetail table {
  width: 100%;
  margin: 30px;
  table-layout: fixed;
  
}

.locationDetail tbody td{
color: #e79216;
background-color:  #2d2928;
  padding: 20px;
  margin: 20px;
  font-size: 20px;
}

/* price detail */

.priceDiv {
  width: 80%;
  margin: auto;
  text-align: center;
}

.priceDetail {
  width: 100%;
  table-layout: fixed;
  font-size: 20px;
}

.priceDetail th {
  padding: 15px;
  background-color: #2d2928;
  color: white;
}

.priceDetail td {
  padding: 10px;
}

.priceDetail button {
  color: #2d2928;
  font-size: 20px;
  padding: 5px;
  margin: auto;
  border: none;
}

.priceDetail button:hover {
  font-weight: bold;
}

.priceDetail tr:nth-child(even) {
  background-color: #f2f2f2;
}

.priceDetail tr:hover {
  background-color: #ddd;
}

/* Contact detail */
.contactDetail  h1 {
  margin: 50px;
  text-align: center;
}
.contactDetail h2 {
  margin: 20px;
  text-align: center;
}

 a:link {
  color: #2d2928;
}

 a:visited {
  color:green;
}

 a:hover {
  color: #e79216;;
}

 a:active {
  color: blue;
}
/* Footer */
/*   header  */
.footer {
  height: 5vh;
  background-color: #ebedf0;
}

.footer>p {
  padding-top: 10px;
  margin-top: 15px;
  border-top: solid #555 1px;
  font-size: 20px;
  text-align: center;
}